@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Jost:wght@400;500;700&display=swap');

$main-blue:#3fc4ff;



.App {
  position: relative;
  background-color: black;
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  overflow-y: scroll;
  -webkit-tap-highlight-color: transparent;
  transition: all 200ms ease-in-out;
}
.App-inner{
  display: flex;
  flex-direction: column;
  align-items: center;
}


.bg-image-holder{
  display:flex;
  align-items:center;
  justify-content:center;
  height: 90vh;
  width: 100vw;
  overflow: hidden;
  z-index:0;
  background-image: url(./assets/images/header.svg);
  background-position: center;
  background-size:cover;
}

@media screen and (max-width:700px){
  .bg-image-holder{
   
    // height: calc(100vw * 0.3875);
    height: 70vw;
    
  }
}

.bg-mint-pass-image{
  width: 100vw;
  height: calc(100vw * 0.3875);
  background-image: url(./assets/images/vip-pass.svg);
  background-size:contain;
}
.main-logo{

  position: absolute;
}

.bg-image{
  width:100vw;
  min-height: 100vh;
  object-fit: contain;
  
}

.d-grid{
  display: grid;
}

.font-xl{
  font-size:44px;
  font-family: 'Montserrat',sans-serif;
  max-width: 80%;
}
.font-l{
  font-size: 2.3rem;
  font-family:'Montserrat',sans-serif;
}
.font-m{
  font-size: 1.7rem;
  font-family: 'Montserrat',sans-serif;
}
.font-s{
  font-size: 1.2rem;
  font-family: 'Montserrat',sans-serif;
}
.font-xs{
  font-size: 1rem;
  font-family: 'Jost',sans-serif;
  
  line-height: 22px;
}



.outer-section{
  .inner{
    text-align: justify;
    max-width: 700px;
  }
}

.display-flex{
  display: flex;
}


.text-white{
  color:#ffffff;
}
.text-gray{
  color:gray;
}
.text-light-gray{
  color:#9ba0a8;
}
.text-blue{
  color:$main-blue;
}
.text-red{
  color:red;
}
.extra-bold{
  font-weight:800;
}

.bold{
  font-weight:bold;
}

.text-normal{
  font-weight: 400;
}
.align-end{
  justify-content: flex-end;
}


.mt-point5{
  margin-top:0.5rem;
}

.mt-1{
  margin-top:1rem;
}
.mt-2{
  margin-top:2rem;
}
.mb-1{
  margin-bottom:1rem;
}
.mb-2{
  margin-bottom:2rem;
}
.mb-4{
  margin-bottom:3rem;
}
.mt-8{
  margin-top:8rem;
}



@media screen and (max-width:800px){
  .font-xl{
    font-size:40px;
  }

  .font-l{
    font-size: 2rem;
  }
  .outer-section{
    .inner{
      text-align: start;
      max-width: 85vw;
    }
  }
  .font-s{
    font-size: 1rem;
    
  }

  .mt-2{
    margin-top:2rem;
  }
  .mb-1{
    margin-bottom:1rem
  }
  .mb-4{
    margin-bottom:1rem;
  }
  .mb-2{
    margin-bottom:2rem
  }
  .mt-8{
    margin-top:2rem;
  }
  


}




.text-center{
  text-align: center;
}
.text-start{
  text-align: start;
}

.center-children{
  display: flex;
  justify-content: center;
  align-items: center;
}


.social-logo{
  height: 16px;
  width: 16px;
 
  cursor: pointer; 

  img{
    height: 100%;
    width: 100%;
  }
}

.contract-address{
  cursor: pointer;
  text-decoration: none;
  &:hover{
    text-decoration: underline;
   
  }
}
.section{
  max-width: 1110px;
}
.outer-section{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  background:#131313;

}

@media  screen and (max-width:500px) and (min-height:500px) {
  .bg-image{
   width: 200vh;
  }
  .font-xl{
    font-size:29px;
    font-family: 'Poppins',sans-serif;
    max-width: 100%;
  }
}

