$white:#ffffff;
$main-blue:#3fc4ff;

.MintSection{
    display: flex;
    position: relative;
    // align-items: center;
    justify-content: space-between;
    width:100%;
    background-color: #000;
    padding: 60px 0 60px 0;
    width:1110px;
    height: 588px;
    box-sizing: border-box;
    transition: all 200ms ease-in-out;
    // background-color: red;
}
.Mintsection-img{
    display: flex;
    align-items: center;
    height: 100%;
    margin-top: -35px;
    // img{
    //     // height: 300px;
    //     width: 400px;
    // }
    video{
        height: 100%;
    }
}



.MintSection-left{
    position: relative;
    display: grid;
   
    flex-direction: column;
    
    height: 100%;

}

.Mint-section-right{
  
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    // height: 100%;
    // justify-content: flex-start;
    text-align: right;
    // background-color: red;
    justify-content: space-between;
}

.mobile-mint-supply{
    display: none;
}



@media  screen and (max-width:900px) {
    .desktop-mint-supply{
        display: none;
    }

    .mobile-mint-supply{
        display: block;
    }

    .MintSection{
        flex-direction: column;
        height: fit-content;
        width: 100vw;
    }
    .Mintsection-img{
        width: 100vw;
        height: 100vw;
        margin-top: 0px;
        margin: auto;
        width: fit-content;
        img{
            height: 200px;
            // margin-top: 45px;
        }
    }
    .MintSection-left{
        display: grid;
        justify-content: center;
        flex-direction: column;
      
    }
    
    .Mint-section-right{
        display: grid;
        flex-direction: column;
        text-align: start;
        justify-content: center;
        // margin-top: 45px;
    }
}

.MintSection-left-bottom{
    display: flex;
    margin-top: 20px;
}
.MintSection-bossNum{
    

}
.MintSection-bossNum-top,.MintSection-bossNum-bottom{
    margin:0 0 20px 0;
    display: flex;
  
    height: 55px;
    width:300px;
    justify-content: space-between;
}
.MintSection-bossNum-bottom{
    margin:0;
    width: 300px;
}
.MintSection-bossNum-top>input{
    height: 51px;
    // flex:1;
    border:2px solid $main-blue;
    outline:none;
    background: transparent;
    max-width: 55%;
    text-align: center;
    border-radius: 12px;
}
.mint-plus-btn,.mint-button,.mint-amount-btn{
    height: 100%;
    aspect-ratio: 1;
    border:2px solid $main-blue;
    border-radius: 12px;
    cursor: pointer;
    background: transparent;
}


.mint-plus-btn:hover{
    background:  $main-blue;
    color:black;
}
#mint-btn{
   
    position: absolute;
    bottom: 0;
}
.mint-button{
    height: 55px;
    width:300px;
    background:  $main-blue;
  
  
}
.mint-button:hover{
    background: transparent;
    color:$white;
}
.mint-amount-btn{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width:30%;
    height:85%;
}
.mint-amount-btn:hover{
    background: $main-blue;
    color:black;
}
.mint-amount-btn>img{
    height:78%;
}
.Mintsection-right-img{
    align-items: flex-start;
 
    height:350px;
    width:350px;
    overflow: hidden;
}
.Mintsection-right-img>img{
    width:100%;

}
.mint-section-middle{
    text-align: center;
    @media screen and (min-width:901px) {
        width:468px;
    }
}
.mint-section-vip-about{
    width: 325px;
    text-align: justify;
}

