$main-blue:#3fc4ff;

.Roadmap-desktop,.Roadmap-mobile{
    position: relative;
    // background-color: red;
    display: flex;
    flex-direction: column;
    align-items: center;
   
}
.Roadmap-desktop{
    .Roadmap-progress{
        position: absolute;
        display: flex;
    
       
        width: 5px;
        background: $main-blue;
        height: 80%;
        margin-top: 50px;
    }
    .progress-circle{
        position: absolute;
        margin-left:auto;
        margin-right: auto;
        left:0;
        right:0;
        height: 20px;
        width: 20px;
        border-radius: 360px;
        background-color: white;
        box-shadow: 0 0 9px 9px rgb(0 0 0 / 70%);
    }
    .Roadmap-section{
        position: relative;
        z-index: 10;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height:100px;
        width: 700px;
        margin-bottom: 12rem;
        p{
            max-width: 35%;
        }
    }
    .roadmap-text-left{
        text-align: left;
    }
    .Roadmap-quarter{
        width:50%;
        display:flex;
        justify-content:center;
    }
}



.Roadmap-mobile{
    display: none;
    .Roadmap-progress{
        position: absolute;
        
       
        width: 5px;
        background: $main-blue;
        height: 80%;
        margin-top: 50px;
    }
    .progress-circle{
        position: absolute;
        margin-left:auto;
        margin-right: auto;
        left:0;
        right:0;
        height: 20px;
        width: 20px;
        border-radius: 360px;
        background-color: white;
        box-shadow: 0 0 9px 9px rgb(0 0 0 / 70%);
    }
    .Roadmap-section{
        position: relative;
        z-index: 10;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height:100px;
        margin-bottom: 12rem;
        p{
            max-width: 35%;
        }
    }
    .roadmap-text-left{
        text-align: left;
    }
    .Roadmap-quarter{
        width:50%;
        display:flex;
        justify-content:center;
    }
}


@media screen and (max-width:800px){
    .Roadmap-desktop{
        display: none;
    }
    .Roadmap-mobile{
        width: 85vw;
        display: flex;
        align-items: flex-start;
        .Roadmap-section{
            // background-color: red;
            justify-content: flex-start;
         
            width: 50vw;
            margin-bottom: 8rem;
            p{
             
                max-width: 75%;
                margin-left: 15vw;
            }
        }
        .progress-circle{
            
            margin-left:-8px;
            margin-right: auto;
           
        }
    }
   
}
@media screen and (max-width:600px){
    .Roadmap-mobile{
    
        .Roadmap-section{
            width: 75vw;
            p{
             
                max-width: 95%;
           
            }
        }
      
    }


}
