.About{
    display: flex;
    justify-content: space-between;
    transition: all 200ms ease-in-out;
    align-items: center;
}

.About-left{
    max-width: 60%;
    text-align: justify;
}
.about-img{
    // display: flex;
    // justify-content: center;
    height:400px;
    width: 400px;
    // background: lightblue;
    border-radius: 20px;
    
    
}
.about-img>img{
    height: 100%;
}
@media screen and (max-width:1150px){
    .About{

        max-width: 90vw;
        align-items: center;
    }
    .about-img{
        height: 30vw;
        width: 30vw; 
    }
}

@media screen and (max-width:900px){
    .About{
        flex-direction: column-reverse;
    }
    .About-left{
        max-width: 700px;
        margin-bottom:2rem;
        text-align: center;

    }
    .about-img{
        width: 700px; 
        height:  700px;
        margin-bottom:2rem;
    }
}
@media screen and (max-width:800px){
   
    .About-left{
        max-width: 85vw;
        text-align: start
    }
    .about-img{
        width: 85vw; 
        height:  85vw;
    }
}