$main-blue:#3fc4ff;

.Header{
    position: sticky;
    top:0;
    z-index: 100;
    height: 110px;
    width: 100vw;
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 55px 0 35px;
    box-sizing: border-box;
}

  


.Header-left{
    height: 100%;
    display: flex;
    align-items: center;
    span{
        margin-right: 20px;
        cursor: pointer;
        &:hover{
            color:$main-blue
        }
    }
}
.Header-right{
    display: flex;
    align-items: center;
    height: 100%;
    width: 100px;
    justify-content: space-between;
}
.header-menu-btn{
    z-index: 30;
    display: none;
    height: 35px;
    background-color: transparent;
    border: none;
    cursor: pointer;
}
.header-menu-btn>img{
    height: 100%;
}


.Header-logo{
    height:55%;
    margin-right: 50px;
}
.Header-mobile-nav{
    display: none;
}
@media screen and (max-width:750px){
    .Header-logo{
        height:65%;
        margin-right: 50px;
       
    }
    .Header{
        height:85px;
        padding: 0 35px 0 35px;
    }
    // .Header-right{
    //     display: none;
       
    // }
    .Header-right{
        position: absolute;
        height: 100%;
        width: 90px;
        margin-left: auto;
        margin-right: 85px;
        left: 0;
        right:0;
        justify-content: space-between;
        // background-color: red;
    }
    .Header-left-nav{
        display: none;
    }
    .Header-mobile-nav{
        display: none;
        overflow:hidden;
        transition: all 500ms ease-in-out;
      
        z-index: 10;
        height: 0vh;
        width: 100vw;
        position:absolute;
        top:0;
        left:0;
        background-color: black;
        .Header-mobile-nav-inner{
            display: flex;
            height: 100vh;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 100vw;
            img{
                margin-bottom: 10vh;
                height: 15%;
            }
            span{
                margin-top:30px;
                cursor: pointer;
                &:hover{
                    color:$main-blue
                }
            }
        }
        
    }
  
    .header-menu-btn{
        display: flex;
    }
}
.social-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    height: 40px;
    width: 40px;
    background-color: white;
    cursor: pointer;
    border:none;

    img{
        height: 84%;
    }
    &:hover{
        height: 45px;
        width: 45px;
    }
}