$main-blue:#3fc4ff;

.team-section{
    max-width: 1110px;
    text-align: center;
    margin-bottom: 5rem;
}
.team-section-bottom,
.team-section-top
{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    // background-color: blue;
    // border: 1px solid red;
}
.team-section-bottom{
    margin-top: 70px;
}
.team-section-bottom-right{
    margin-left:135px
}
.team-section-bottom-right,
.team-section-bottom-left
{
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.team-wrapper{
    z-index: 10;
    height: 400px;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    img{
        height: 80%;
        border-radius: 50%;
    }
}
.team-chain{
    position: relative;
    height: 400px;
    position: absolute;
    width: 120px;
    border: 5px solid $main-blue;
}

.team-chain-top{
    position: relative;
   
    position: absolute;
  
    border: 5px solid $main-blue;

    margin-top: 520px;
    border-right:none;
    border-radius: 20px;
    height: 425px;
    width:600px;
}
.team-chain-left{
    margin-top:-60px;
    left:-100px;
    border-right:none;
    border-radius: 20px;
}
.team-chain-right{
    right:-100px;
    margin-top:-60px;
    border-left: none;
    border-radius: 20px;
}

@media screen and (max-width:950px){
   
    .team-section{
        max-width: 85vw;
      
    }
    .team-chain-top{
        margin-top: 350px;
        height: 390px;
        width:400px;
    }
    .team-chain{
        height: 350px;
        
    }
    .team-chain-left{
        left:-70px;
    }
    .team-chain-right{
        right:-70px;
    }
    .team-wrapper{
   
        height: 350px;
        width: 262px;
       
    
    }
}
@media screen and (max-width:900px){
   
    .team-chain-top{
        height: 305px;
        width:400px;
    }
  
    .team-wrapper{
        
        height: 300px;
        width: 225px;
       
    }
    .team-section-bottom-right{
        margin-left:85px
    }
}
@media screen and (max-width:750px){
    .team-chain{
        height: 320px;
        margin-top: -110px;
    }
    .team-chain-top{
        height: 265px;
        width:400px;
       
    }
    .team-chain-left{
        left:-50px;
    }
    .team-chain-right{
        right:-50px;
    }
    .team-wrapper{
        height: 250px;
        width: 187px;
    }
    #wrapper-bottom{
        margin-bottom: 70px;
    }
}

@media screen and (max-width:600px){
    .team-section-bottom-right{
        margin-left:12vw
    }
    .team-chain{
        height: 55vw;
        margin-top: -110px;
    }
    .team-chain-top{
        margin-top: 38vw;
        height: 43vw;
        width:45vw;
    }
    .team-chain-left{
        left:-40px;
    }
    .team-chain-right{
        right:-40px;
    }
    .team-wrapper{
        height: 40vw;
        width: 30vw;
    }
    #wrapper-bottom{
        margin-bottom: 70px;
    }
}
@media screen and (max-width:400px){
 
    .team-chain{
        height: 57vw;
        margin-top: -100px;
    }
